export default [{"id":"8","content":"一个杂记本\n\n<strong>-- 读书清单 --</strong>\n\n<i>- 已读 -</i>\n全球通史（上）\nNonviolent Communication\n\n<i>- 备选 -</i>\n\b创新者的窘境\n如何阅读一本书\n全球通史（下）\n漫步华尔街\n非理性繁荣\n时间简史\n未来简史\n影响力\n\n<strong>-- blog features --</strong>\n\n<s>quote</s>\n<s>responsive</s>\ncss-in-js\n字数统计\n标签统计\njs hash\nfavicon\n\n","title":"置顶 · 杂记本","time":""},{"id":"6","content":"一周时间看完了To Kill a Mockingbird，必须赞一下Kindle，是读书神器。\n\n--\n\n故事讲述的\b是1930年代美国Alabama州一个叫Maycomb镇子（我查了一下应该编造的）发生的事情。主人公Scout是一个6-9岁的小女孩。故事的主线有两条。\n\n<i>一条是关于racist和勇气</i>\n\n主人公的父亲Atticus是一个律师，他被委任成为黑人Tom Robinson的律师，Tom被起诉强奸了镇子上的白人姑娘Mayella Ewell。虽然案件中的证据都明显指明Tom是被诬陷的，（真相是Mayella诱惑Tom被父亲当场抓获，然后遭到殴打污蔑Tom强奸）。但最终法庭还是宣判了Tom的guilty，最后Tom在脱狱的时候被射杀。\n\n“Yes, suh. I felt right sorry for her, she seemed to try more’n the rest of ‘em—”\n“You felt sorry for her, you felt sorry for he?”\n\n法院上，当Tom说他帮助Mayella是因为他sorry她的时候，氛围上的改变简直是精妙，一场源于种族歧视的悲剧也注定了发生，无论证据如何明显。\n\n“I think there’s just one kind of folks. Folks.”\n\n“I wanted you to see what real courage is, instead of getting the idea that courage is a man with a gun in his hand. It’s when you know you’re licked before you begin but you begin anyway and you see it through no matter what. You rarely win, but sometimes you do.”\n\n<i>一条是关于偏见和善良</i>\n\nBoo Radley是家访邻居，传闻Boo以前是个很恶劣的人，但是已经很久没人见过他了，他从不出门。Scout和Jem对他充满好奇，随着故事的进展他们从原先无比害怕他，到他 leaves Jem and Scout gifts之后又帮助mends Jem’s pants，他们对他更多是好奇和想见上一面。故事的最后，当Bob Ewell 想要杀害Jem and Scout的危难时候，Boo 终于出现救了他们的性命。\n\n“Atticus, he was real nice.” “Most people are, Scout, when you finally see them.”\n\n\b--\n\n刚看完的一瞬间我都没有想明白为什么这本书叫To Kill a Mockingbird，看了一些网评才想明白很多细节，Mockingbird寓意的是善良。\b\b\n\n当Atticus和Tate发现是Boo Radley杀死Bob救了Scout和Jem之后，\n\n\"Mr. Finch, taking the one man who's done you and this town a great service and draggin' him with his shy ways into the limelight ---to me that's a sin.....If it was any other man it'd be different.  But not this man, Mr. Finch.\"\n\nTate想到Boo不希望因为收到更多关注后，就决定判断Bob Ewell是以外杀死了自己。Atticus想通这件事之后就问Scout是否明白了其中缘由。Scout说\n\n\"Well, it'd be sort of like shootin' a mockingbird, wouldn't it?\"\n\n--\n\n嗯，不要辜负善良\n","title":"Books · To Kill a Mockingbird","time":"2020.02.10"},{"id":"5","content":"Reading - 9.3/10\nWorkout - 5/5\n\n第一周wfh，最大的感受大约就是工作没效率\n\n9:00 - 9:30：起床吃点东西准备上班（认真讲，不用上下班commute的时间还是很爽的），穿插着读读书\n17:00: 下班，健身到18:00\n19:00: 吃完晚饭，下棋/德扑 + 读书\n\n没效率主要几个原因：\n1. 在家里缺少peer pressure让我有动力干活\n2. 身上的任务比较缥缈，没有deadline，需要self-driven。在这个时候就很难motivate自己，写文档也是我的弱项，本来好不容易写了一个文档，又觉得不理想又重新推翻重写\n3. 等待和discuss landing的offer\b\b\b占据了很多精力，无心工作\n\n这周意几乎完成了10个小时的阅读任务，自己都有点意外。《杀死一只知更鸟》本身其实并没有那么好看，但可能kindle体验确实很好。周中每天看一个小时，（了周二因为等offer睡得太少），周末读2-3个小时，希望下周可以保持。除此以外也完成了5次运动。下周接着保持运动3，4天休息一天的节奏。\n\n--\n\n这周拿到landing offer，Naixin也接了global offer，算是解决这个让我们纠结很久的选择。虽然嘴上说回美国回美国，但我想我们都没有想到真的会发生的这么快吧，至少我没有，至少我们可能一个月前也没有。世事难料，计划总是赶不上变化。\n\n回想一下，这一切确实发生的太突然太快，\b做这些选择也确实都是最好的选择，只是恰好他们都是回美国的选择。\n\n加入landing是一个挑战，至少这个挑战还没有开始已经让我学习了很多东西。最近学习了很多创业公司的概念，option，stock，融资有关的信息。想想回国前还想过创业，真的是有点naive吧。\n\n有的时候我真的不得不去相信命运，相信我在被命运眷顾。在最及时的时候，总会出现一个理想的机会让我去把握。嘴上说着这都是我的努力成就的，是因为自己优秀才有这些机会，但是心底里却实实在在敬畏着这份命运的安排。人越是长大，越是明白自己的渺小，社会，命运，在这些力量面前我们无所适从，只能顺流。\n\n--\n\n\b说一下这周生气的事情，这周生气了两次。如果说这两件事有什么收获，那就是意识到虽然我学习了NVC，但当被情绪控制的时候，都是无法发挥他的效果，在想真正用好NVC之前，先要学会处理自己的情绪（也或许处理自己的情绪就是NVC）\n\n<i>第一次</i>，实在讲是我的问题，我没有任何道理。我因为等待结果太焦虑，结果无缘无故的发泄在了naixin身上。我都没有脸面去承认，这其实跟我是不是想早睡，你是不是早早跟我语音了一点关系没有，只是我太焦虑，任何不顺心的事情都会像扎在气球上的针，让它爆炸。\n\n焦虑是我担心最后没有拿到landing offer。担心没拿到是因为我投入了精力准备，告诉了你和爸妈，我的自尊心压在了这上面。我害怕失败，\b害怕因为我要为你没法去美国负责，或者我害怕你就去了我一个人留下了。害怕是我的虚荣心作祟，承担不起一次大家都在期待的事情的失败，我接受不了。\n\n如果是这样，怎么消除它了。我也没想好，可能是告诉我如果没拿到我们就留下（你好像讲了？）。可能是告诉我这本身就是一个机缘巧合的机会，失去了我们还是回到一个月前，一切都没有损失。\b可能是告诉我不要那么强虚荣心，你最亲近的人才是不会因为一次失败看不起你的人，你可以暴露你的脆弱和局限。\n\n<i>第二次</i>，因为你再次推迟你回来的时间，我生气了。\b关于这件事，我反而更能理解我自己，我不认为我的需求有问题，我也不认为我的观点有问题。\n\n如果对待生死风险的\b衡量不一致本来就是我们很大的不同，那这件事就是价值观不同本人。你认为回来有危险，我认为没危险。你认为晚一周回来更安全，其实我不知道，但我已经不满你不愿承担回来的风险了，所以你晚点回来是不是更安全，可能本身不重要了。\n\n另一方面，\b\b\b我内心现在有一个对你爸爸妈妈的偏见，我偏见的认为他们不喜欢我，认为他们没有在祝福我们。就好像我们在语音时候你妈妈叫你，我会想她不知道我们在打电话么，这么打扰考虑我的感受么。所以他们在不希望你早回来这件事上也不考虑我们想早点彼此相见么？有一点点考虑到你还有男朋友在北京等你回来么？\n\n我知道这个偏见可能不对也不好，但这个需求的空虚也确实存在，没法简单自愈。\n\n所以我不认为自己的需求有错，我也不认为自己的判断（至少在风险这件事上）有错。那我哪里可以做的更好呢？我不应该让情绪掌控我的需求。\n\n读To Kill A mockingbird 里，主角的父亲是一个让人着迷的人。Atticus 对很多事情有自己的看法，也有很多自己的需求，但是他表达自己的方式总是很温文尔雅，好像没有什么可以让他暴跳如雷，它可以谦逊的表达自己所有的观点。\n\n这或许是我欠缺的吧，可以有需求，有不一样的想法，但始终沉着冷静，always keep calm\n\n--\n\n呼吸\n\b我为什么在生气\n自己什么需求没有被满足\n需求来自何处\n\n","title":"Weekly · 20.06","time":"2020.02.09"},{"id":"4","content":"Reading - 6/10\nWorkout - 5/5\n\n第一次按时写Weekly! 以后要keep up。\n\n--\n\n上周最后还是没有达成10小时读书计划，准备面试的两次几乎没看，外加这次Weekly只有6天，也算是可以理解吧！这周读完了NVC，并写了读后感，我觉得还是不错的，写一遍对于加深印象还是很有帮助的。希望以后都可以做到。\n\n在选择下一本这件事让我头疼了一小下。慢慢发现选择一本读的下去的书还是一件挺难的事情。最后选择了《杀死一只知更鸟》，算是读个故事让自己放松一下吧。哦对了，我还是买了kindle，so far还不错，一开始确实不太适应，有点像个古董，但是用顺了觉得还不错。最大的优点是能选择自动翻译，还挺有帮助的，尤其感觉后面一段时间都大部分会读英文书。\n\n--\n\n这种6天运动了5天，完成这个achievement也是指的嘉奖的，下周加油！\n\n--\n\n面完landing了，虽然不想太自信，但是感觉整体问题应该不大吧。上次应何总邀请去讲前端面试似乎起到了不小作用，所有面试官（除了Andrew本人）都是让天在场的，明显感觉他们都对我很nice，他们也说面试题目都是被我启发的，都让我面的压力很大哈哈。几个面试中都有一些point觉得我做的不够好，但是整体还是差强人意吧。\n\n下面就是等周二出结果了，焦虑的感觉明后天都没办法明认真上班了lol。不过反正也是wfh，水一点就水一点吧。\n\n--\n\n武汉病毒猖獗的厉害，这一周都是在家度过的，除了去了两次圣熙八号+几次去楼下取京东快递。剩余时间都没出门。这几天的Routine也比较固定，读书，下棋，运动，看电影，准备面试。虽然还是没有达到每周阅读的10h要求，但是我还是对自己比较满意了。高中以后在家的时间我都是颓废过去的，这周大约已经是2010年以来在家效率最充实的一段时间了，至少没有玩游戏:)\n\n这一周（其实也包括上周）也是很久以来和爸爸妈妈相处最多的一段时间，我挺开心的，我想他们也挺开心的。有一天爸爸说这样的在一起的机会以后也不多了，忽然有点小伤感。趁着现在可以，就多陪陪他们吧。\n\n\n","title":"Weekly · 20.05","time":"2020.02.02"},{"id":"3","content":"<strong>-- 读后感 --</strong>\n\nNVC核心的观念在于沟通重在双方的感受和需求，表达自己的感受和需求，同情别人的感受和需求。\n\n1. 挖掘自己的感受和需求是什么，尤其是生气的时候，明白不是别人让我生气，是自己的需求没有被满足。\n2. 表达的时候按照NVC，去表达自己的感受和需求。\n3. 聆听别人的时候不做judgments，empathize对方的感受。\n\n<strong>-- 每章摘要 --</strong>\n\n<i>1. Giving From the Heart</i>\n\n<q>NVC: a way of communicating that leads us to give from the heart.\n  We perceive relationships in a new light when we use NVC to hear our own deeper needs and those of others.\n  Four components of NVC: 1. observations 2. feelings 3. needs 4.requests.\n  Two part if NVC: 1. express honestly through the four components. 2. receive empathically through the four\n  components.</q>\nNVC的核心理念是沟通应该focus on 人的feelings and needs。世界上所有人的feel 和 needs是共同的。如此沟通能解决所有障碍。\nNVC四要素1. observations 2. feelings 3. needs 4.requests。在自我表达的时候honest表达四要素\b，聆听别人的时候利用empathy发现别人的feelings and needs.\n\n<i>2. Communication That Blocks Compassion</i>\n\n<q>Certain ways of communicating alienate us from natural state of compassion.\n  In the words of judgments, our concern centers on \"who is what\".\n  Analyses of others are actually expressions of our own needs and values.\n  Classifying and judging people promotes violence.\n  Comparisons are a form of judgment.\n  Our language obscures awareness of personal responsibility.\n  We can replace languages that implies lack of choice with language that acknowledges choice.\n  We are dangerous when we are not conscious of our responsibility for how we behave, think and feel.\n  We can never make people do anything.\n  Thinking based on \"who deserves what\" blocks compassionate communication.\n  Life-alienating communication has deep philosophical and political roots.</q>\n沟通时候杜绝做judgment，不做比较，才可以有compassion.\n\n<i>3. Observing Without Evaluating</i>\n\n<q>When we combine observation with evaluation, people are apt to hear criticism.</q>\nobserve的时候做到客观，不做分析\n\n<i>4. Identifying and Expressing Feelings</i>\n\n<q>Expressing our vulnerability can help resolve conflicts.\n  Distinguish feelings from thoughts.\n  Distinguish between what we feel and what we think we are.\n  Distinguish between what we feel and how we think others react or behave towards us.</q>\n挖掘真是内在的感受，不带任何思考，不受别人影响\n\n<i>5. Taking Responsibility for Our Feelings</i>\n\n<q>What others do may be the stimulus of our feelings, but not the cause.\n  Four options for receiving negative messages: 1. blame ourselves. 2 blame others. 3. sense our own feelings and needs.\n  4. sense others' feelings and needs.\n  Connect your feeling with your needs: \"I feel ... because I need ...\".\n  Distinguish between giving from the heart and being motivated by guilt.\n  Judgments of others are alienated expressions of our own unmet needs.\n  If we express our needs, we have a better chance of getting them met.\n  If we don't value our needs, others mat not either.</q>\n感受是自己的，别人可能刺激，但是不会产生你的感受。从自己的感受出发发觉自己的需求，不要从愧疚中。不对别人做judgments或者criticism，重视自己的感受（或者别人的）。\n\n<i>6. Requesting That Which Would Enrich Life</i>\n\n<q>Use positive language when making request.\n  Making requests in clear, positive, concrete action language reveals what we really want.\n  Vague language contributes to internal confusion.\n  Depression is the reward we get for being \"good\".\n  Request might sounds like demand when unaccompanied by the speaker's feeling and needs.\n  To make sure the message we sent is the message that's received, ask the listener to reflect it back.\n  After we express ourselves vulnerably, we often want to know 1) what the listener is feeling, 2) what the listener is\n  thinking. 3) whether the listener would be willing to take a particular action.\n  In a group, much time is wasted when speakers aren't certain what response they're wanting.\n  Demand vs Request: demand can be only submitted or rebelled.\n  Our objective is a relationship based on honesty and empathy.\n</q>\n了解Needs之后是一个明确的request，一定要明确避免一切模糊。最好让listener重复一遍。在给一个group\ncommunicate的时候一定要明确我们想到得到什么。request不是demand，不是只有做或者不做，不做批判或者judge，不造成guilt，如果对方不同意，用empathy去理解对方对方的needs.\n\n<i>7. Receiving Empathically</i>\n\n<q>Empty our mind and listening with our whole being.\n  Ask before offering advice or reassurance.\n  Intellectual understanding blocks empathy.\n  Listen to what people are needing rather than what they are thinking.\n  When ask for information, first express our own feelings and needs.\n  Paraphrase only when it contributes to greater compassion and understanding.\n  Paraphrasing saves time.\n</q>\n作为听着，不要着急去说自己的建议或者想法，挖掘倾诉者的feelings and needs，只听。给建议之前先询问。\n\n<i>8. The Power of Empathy</i>\n\n<q>\"Don't just do something...\"\n  It's harder to empathize with those who appear to possess more power, status or resources.\n  The more empathize with the other party, the safer we feel.\n  Rather than put your \"but\" in the face of an angry person, empathize.\n  It may be difficult to empathize with those who are closest to us.\n  That bores the listener bores the speaker as well.\n  Speakers prefer that listeners interrupt rather than pretend to listen.\n  Empathize with silence by listening for the feelings and needs behind it.\n</q>\n如何更好地去Empathize？还是去抓住对方的感受，这对亲人和上级比较难一些。另外适当的interrupt去empathize对方的感受，甚至可以适当的沉默。\n\n<i>9.Connecting Compassionately With Ourselves</i>\n\n<q>NVC's most important use may be developing self-compassion\n  We use NVC to evaluate ourselves in ways that engender growth rather than self-hatred.\n  Avoid shoulding yourself!\n  Self-judgments, like all judgments, are tragic expressions of unmet needs.\n  NVC mourning: connecting with the feelings and unmet needs stimulated by past actions we now regret.\n  NVC self-forgiveness: connecting with the need we were trying to meet when we took the action that we now regret.\n  We are compassionate with ourselves when we are able to embrace all parts of ourselves and recognize the needs and\n  values expressed by each part.\n  We want to take action out of the desire to contribute to life rather than out of fear, guilt, shame, or obligation.\n  With every choice you make, be conscious of what need it serves.\n  Be conscious of actions motivated by the desire for money or approval, and by fear, shame, or guilt. Know the price\n  you pay for them.\n  The most dangerous of all behaviors may consist of doing things \"because we're supposed to\".\n</q>\n本书最重要（自认为）是对自己使用NVC，要永远明白自己的做的事情在serve自己的什么need。\n不要因为fear, guilt, shame, or obligation做事情，如果做了，也要明白这里面的代价。\n不要说自己”应该“做什么，应该是外部施加的，而应该从自己的内在需求，说”需要“什么所以要做什么。\n如果regret做了什么事情，去思考当时有什么need没有被满足，或者我们当时在尝试满足什么need，而是行为不对。\n\n<i>10.Expressing Anger Fully</i>\n\n<q>Hurting people is superficial.\n  We are never angry because of what others say or do.\n  To motivate by guilt, mix up stimulus and cause.\n  The cause of anger lies in our thinking - in thoughts of blame and judgment.\n  When we judge others, we contribute to violence.\n  Use anger as a wake-up call.\n  When we become aware of our needs, anger gives away to life-serving feelings.\n  Violence comes from the belief that other people cause our pain and therefore deserve punishment.\n  Judgments of others contributes to self-fulfilling prophecies.\n  Steps to expressing anger: 1. Stop.Breathe. 2. Identify our judgmental thoughts. 3. Connect with our needs. 4. Express\n  our feelings and unmet needs.\n  Stay conscious of the violent thoughts that arise in our minds, without judging them.\n  Our need is for the other person to truly hear our pain.\n  People do not hear your pain when they believe they are at fault.\n  Take your time.\n</q>\n本书对我最重要的一章，如何处理Anger。\n我们anger不是因为别人做了什么，而且是源自自己的needs。\n因为anger去伤害别人是肤浅的，没办法帮助我们实现自己的need。\n当生气的时候。1. 深呼吸 2. 把握生气时候的想法 3. 挖掘自己的需求是什么。4. 通过NVC表达自己的需求。\n慢慢来。\n\n<i>11. Conflict Resolution and Mediation</i>\n\n<q>Creating a connection between people is the most important thing.\n  When you make the connection, the problem usually solves itself.\n  Avoid the use of language that implies wrongness.\n  Learn to hear needs regardless of how people express them.\n  Criticism and diagnosis get in the way of peaceful resolution of conflict.\n  People often need empathy before they are able to hear what is being said.\n  The objective is not to get the other parties to do what we want them to do.\n  Role-play is simply putting ourselves in the other person's shoes.\n</q>\n处理conflict的时候最重要的是让双方可以交流彼此的needs。 当双方能听到对方的needs问题就自然会解决。如果有一方有太多pain我们就empathy，听到no不是rejection，是一个机会我们我需要进一步了解needs.\n\n<i>12. The Protective Use of Force</i>\n\n<q>The intention behind the protective use of force is only to protect, not to punish, blame or condemn.\n  Fear of corporal punishment obscures children's awareness of the compassion underlying their parents' demands.\n  When we fear punishment, we focus on consequences, not on our own values.\n  Fear of punishment diminishes self-esteem and goodwill.\n</q>\n\n使用force的主要intention只能是protect，punish会产生敌意和抵触心理，即使可以实现你希望别人做什么的结果，但是别人的motivation还是很低，无法长期维持。\n\n<i>13. Liberate Ourselves and Counseling Others</i>\n\n<q>The ability to hear our own feelings and needs and empathize with them can free us from depression.\n  Focus on what we want to do rather than what went wrong.\n  Defuse stress by hearing our own feelings and needs.\n  Defuse stress by empathizing with others.\n  I empathized with clients instead of interpreting them; I reveled myself instead of diagnosing them.\n</q>\n\nNVC帮助inner communication，当我们有负面情绪的时候，把他们转换成feelings and needs然后去empathy. 发现自己深处的需求而不是怪罪自己或者别人.\n\n<i>14. Expressing Appreciation in NVC</i>\n\n<q>Compliments are often judgments - however positive-of others.\n  Express appreciation to celebrate, not to manipulate.\n  Saying \"thank you\" in NVC: \"this is what you did; this is what I feel; this is the need of mine that was met.\"\n  Receiving appreciation without feeling of superiority or false humility.\n</q>\n表达appreciation也会从NVC做起，表达对方做了什么，你的感受如何，你的什么need被fulfill了。不要试图通过appreciation去操控别人，长期无效。\n","title":" Books · Nonviolent Communication","time":"2020.02.01"},{"id":"2","content":"Reading - 4.8/10\nWorkout - 1/7\n\n今天周一，上个周记是周三写的，所以这次周记只记录了5天，1天在南昌，1天在泾县，1天在路上，2天在路上。\n\n作为了一个短暂的5天周记，完成了一半的读书计划还是不错的。但实际上都是最近三天很努力的在读书，前面两天并没有读书。最近都是假期，理论上我没有什么理由（太忙）不去读书。所以假期前几天（尤其在南昌的时间？）还是没有好好利用起来读书吧。\n\n运动方面，在寒冷的南昌和泾县是很难昨天运动了。今天回到北京家里完成了做一组胸肌+HIT，有点累。每天做两个训练的组合算作一天运动。我要把运动降到一周5次了。\n\n--\n\n武汉肺炎愈发的严重了，看着每天上涨的数字都觉得吓人。不过对我个人经历来说，这次流感倒不完全是坏事吧（当然，这里面有个忙碌乐观的前提，那就是我相信自己不会中招:D）。\n\n一来，这个流感让我有更多的机会和爸妈在一起，从去厦门初七回，到在泾县初四回，到最后临时初二就回到北京了，我们一起做了不少决定。现在一家人困在北京家里不出门，我能感觉到其实爸爸妈妈还是比较高兴能和我多待在一起一些时间的。\n\n二来，这个流感也打乱了本来旅游的机会，外加流感延迟了上班的时间并且给了额外的wfh的时间，\b我又更多'宅'的时间做例如看书，准备面试，运动，学习，陪爸妈等等事情。至少北京的第一天过得很充实，希望后面的一周都能如此吧。\n\n--\n\n读Non Violent Communication，终于读到了我觉得比较有用的一部分：如何在自己身上使用NVC。\b前面讲的很多NVC实用性并不高，因为现实中非常难真的去实践一些理论，像一个复读机一样去重复别人的话也太傻了点吧！\n\n不过把NVC 落实在自己身上，是一个很practical也对我很必要的事情。我觉得自己很需要NVC去学会如何控住自己的生气，同时不去被敏感的shame, guilt去操控行为，多从自己内心的needs出发，筛选真正想做的事情并给与足够的动力。我现在就能想到过去一个月一些或大或小的事情我可以用来分析解析帮助转变为对自己needs的认知。\n\n具体的总结在写读书感的时候再写吧\n\n--\n\n说说这一周的期待吧，首先，这一周我要完成每周10h的读书计划！如果这周都完成不了，我没理由去相信我以后也能做到。这周要之前运动五次！然后好好准备面试，每天和爸爸下棋。每天的计划要大致如下\n\n上午：9点起床，做题/学习/coursera\n下午：看书，下棋，运动\n晚上：看书，做题/学习/coursera，打电话，1点之前睡觉。\n\nAnyways差不多这样的排列组合吧。\n\n\n","title":"Weekly · 20.04","time":"2020.01.27"},{"id":"1","content":"Reading - 2/10\nWorkout - 3/7\n\n对不起，周记这次又迟到了3天，上个周日补班就忘记了。现在赶上春节放假，先把上周的周记补上。\n\n读书，non violent communication 还是不错的，本来以为英文读物需要适应适应，但读起来发现做着用词还是比较常规的。反倒是一些引用别人的话总是晦涩难懂。读书目标的最大blocker还是玩游戏太多。从今天开始我要人人真真的戒fifa了。下个week一定要完成10个小时的读书计划。\n\n运动，这周去了一次健身房，office workout了两次。感觉除非天暖又开始踢球打球，现在看来每天运动的指标还是很难的。\n\n两个指标还是不达标的，我需要加油了呀。\b\n\n过去一周（算到今天），最大的事情是和landing.ai的Hr小姐姐来北京聊了聊加入的事情。原本随着上次和何总两个Eng的灵光一现一拍即合被拍死之后这件事就消停了。我以为又回到了Airbnb的正常职业发展track上来... 当然这没有什么可抱怨的，过去一年我greatly exeed 之后升了L5，成功的完成了从performance到miniapp的换组，现在也在right track上去成为这个组的EM，可以说这里的职业发展是顺风顺水。被赏识，被提拔，我觉我完全不能ask for more了。。\n\n但是当和小姐姐聊完 landing.ai 的机会，我不得不承认，这个挑战太有吸引力了。\n\n职业规划来讲，我一直相信（就像很多人一样），我终究会尝试加入一家早期的startup去面对更大的挑战。但是选择startup并且太难了，对于内心深处保守的我来说，我没办法相信大部分startup会成功。\b但是landing.ai是andrew ng创建的，我还能找到比andrew更让我信服安心的ceo么？landing.ai里面有何总，我还能找到比何总更让我愿意在一家startup工作的朋友么？？\n\n生活愿景来讲，如果我们终究要回到美国，回到湾区。landing.ai愿意就接下来的4月份抽签直接做抽2次H1b，我还能有更好的方式回到美国么？？Airbnb的L1？再次读书？\n\n一家令我信服(还是做ai的)的startup，帮助我回美国，这个诱惑，实在太大。\n\n\b然而我还是不得不犹豫，一方面是我需要考虑nx的选择，另一方面也是airbnb现在的机遇也真的很好。。我过得这个如鱼得水，我很快要成为EM，我的前景是无比美好的，Airbnb也可以让我L1回到美国，也许...在这里做着做着就不会回到美国呢？未来会怎么样，谁都说不好...\n\n道理告诉我们，人生的重要的转折，都是发生在年轻时的不刻意间。然而这个转折，大的肉眼可见。\n\n我到底在追求什么，我想要生活在哪里，又想要什么样的生活。。\n","title":"Weekly · 20.03","time":"2020.01.22"},{"id":"0","content":"Reading - 3.4/10\nWorkout - 3/7\nSnowboarding - 1\n\n\b这周读书和运动都是不达标的，加油吧。\n\n这周发生的一些事\n1. 开始去健身房，30分钟器械+30游泳的组合好像不错。这周去了2次，感觉还不错\n2. 找到了比较不错的Time tracker，希望可以一直用下去\n3. 玩的游戏还是有点多，要慢慢少玩一点\n4. 看的电视还有点多，要少看一点\n5. 读书可以多一些\n\n这周终于把《全球通史（上）》看完了，回头写个读书笔记吧。不要懒掉。\n\n好吧，万事开头难，这周就不太为难自己了，就这样吧哈哈哈哈。\n\n","title":"Weekly · 20.02","time":"2020.01.13"}]